import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import { getImagesByQuery, PER_PAGE } from './js/pixabay-api';
import { refs } from './js/refs';
import {
  clearGallery,
  createGallery,
  hideLoader,
  hideLoadMoreButton,
  showLoader,
  showLoadMoreButton,
} from './js/render-functions';

let page = 1;
let searchQuery;

refs.searchForm.addEventListener('submit', async e => {
  e.preventDefault();

  searchQuery = e.target.elements['search-text'].value.trim();
  if (!searchQuery) {
    iziToast.error({ message: 'Please, fill the input', position: 'topRight' });
    return;
  }

  page = 1;
  clearGallery();
  hideLoadMoreButton();
  showLoader();

  try {
    const { hits, totalHits } = await getImagesByQuery(searchQuery, page);

    if (!hits?.length) {
      iziToast.error({
        message:
          'Sorry, there are no images matching your search query. Please try again!',
        position: 'topRight',
      });
      return;
    }

    if (page >= Math.ceil(totalHits / PER_PAGE)) {
      iziToast.info({
        position: 'topRight',
        message: "We're sorry, but you've reached the end of search results.",
      });
    } else showLoadMoreButton();

    createGallery(hits);
    e.target.reset();
  } catch {
    error => {
      iziToast.error({
        message: `There is an error with searching images: ${error}`,
        position: 'topRight',
      });
      console.log(error);
    };
  } finally {
    hideLoader();
  }
});

refs.loadMoreBtn.addEventListener('click', async () => {
  hideLoadMoreButton();
  showLoader();

  try {
    const { hits, totalHits } = await getImagesByQuery(searchQuery, page + 1);
    createGallery(hits);
    page++;

    if (page >= Math.ceil(totalHits / PER_PAGE)) {
      iziToast.info({
        position: 'topRight',
        message: "We're sorry, but you've reached the end of search results.",
      });
    } else showLoadMoreButton();

    window.scrollBy({
      top: refs.gallery.firstChild.getBoundingClientRect().height * 2,
      behavior: 'smooth',
    });
  } catch {
    error => {
      iziToast.error({
        message: `There is an error during loading more images: ${error}`,
        position: 'topRight',
      });
      console.log(error);
    };
  } finally {
    hideLoader();
  }
});
